.navbar {
  height: auto;
  width: 100%;
  background: #343a40;
  overflow: auto;
  -webkit-box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.75);
          box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0px;
  z-index: 2; }

.item-list {
  padding: 0px;
  margin: 0px .5em 0px 0px;
  text-decoration: none;
  list-style: none;
  float: right; }
  .item-list .nav-item {
    padding: 8px;
    display: inline-block; }
    .item-list .nav-item a {
      font-size: 30px;
      font-family: 'Product Sans Regular';
      color: rgba(255, 255, 255, 0.8);
      text-decoration: none; }
      .item-list .nav-item a:hover {
